import React, { Component } from 'react';
import './App.css';
import { Map, TileLayer, ZoomControl } from 'react-leaflet';
import 'leaflet.markercluster';
import _ from 'lodash';
import Icon from './Icon.js';
import BlueIcon from './BlueIcon.js';
import Papa from 'papaparse';

const tileURL = 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/light_all/{z}/{x}/{y}.png';
const tileAttr = 'Map tiles by Carto, under CC BY 3.0. Data by OpenStreetMap, under ODbL';

const mapCenter = [41.828, -87.7];
const zoomLevel = 10;

export default class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      selectedView: null,
      data: null,
      antiviolenceGrants: null,
    };

    this.onClickButton = this.onClickButton.bind(this);
    this.onFetchMedExamData = this.onFetchMedExamData.bind(this);
    this.onFetchAntiViolenceData = this.onFetchAntiViolenceData.bind(this);
    this.addDataToMap = this.addDataToMap.bind(this);
  }

  onClickButton = (event) => {
    this.setState({ selectedView: event.target.name });
  }

  componentDidMount() {
    this.fetchMedExamData(this.onFetchMedExamData);
    this.fetchAntiViolenceData(this.onFetchAntiViolenceData);
  }

  componentDidUpdate() {
    this.addDataToMap();
  }

  removeMapLayers() {
    const map = this.leafletMap.leafletElement;

    map.eachLayer((layer) => {
      if (layer.layerID === 'MARKER_CLUSTER_LAYER') { map.removeLayer(layer); }
    });
  }

  addDataToMap() {
    const {selectedView} = this.state;
    if (!selectedView) return null;

    if (selectedView === 'gun-related-deaths') return this.addGunDeathsToMap();

    if (selectedView === 'antiviolence-programs') return this.addAntiviolenceProgramsToMap();

    return this.removeMapLayers();
  }

  addGunDeathsToMap() {
    this.removeMapLayers();
    const map = this.leafletMap.leafletElement;
    const markers = L.markerClusterGroup({ chunkedLoading: true }); // eslint-disable-line
    markers.layerID = 'MARKER_CLUSTER_LAYER';
    const markerList = [];

    _.forEach(this.state.data, (place) => {
      const gunRelated = (place['Gun Related'] === 'true');
      if (!gunRelated) return null;

      const lng = parseFloat(place['longitude']);
      const lat = parseFloat(place['latitude']);

      if (lat && lng) {
        const latLng = L.latLng(lat, lng);                // eslint-disable-line
        const marker = L.marker(latLng, {icon: Icon});    // eslint-disable-line

        marker.bindPopup(`
          Manner of Death: ${place['Manner of Death']}<br/><br/>
          Primary Cause: ${place['Primary Cause']}<br/><br/>
          Race: ${place['Race']}<br/><br/>
          Latino: ${place['Latino']}<br/><br/>
          Gender: ${place['Gender']}<br/><br/>
          Date of Death: ${place['Date of Death']}`);

        markerList.push(marker);  // eslint-disable-line
      }
    });

    markers.addLayers(markerList);
    map.addLayer(markers);
  }

  addAntiviolenceProgramsToMap() {
    this.removeMapLayers();
    const map = this.leafletMap.leafletElement;
    const markers = L.markerClusterGroup({ chunkedLoading: true }); // eslint-disable-line
    markers.layerID = 'MARKER_CLUSTER_LAYER';
    const markerList = [];

    _.forEach(this.state.antiviolenceGrants, (place) => {
      const lng = parseFloat(place['longitude']);
      const lat = parseFloat(place['latitude']);

      if (lat && lng) {
        const latLng = L.latLng(lat, lng);                    // eslint-disable-line
        const marker = L.marker(latLng, {icon: BlueIcon});    // eslint-disable-line

        marker.bindPopup(`
          Organization: ${place.organization}<br/><br/>
          Amount: ${place.amount}<br/><br/>
          Description: ${place.description}<br/><br/>
          Year: 2016`);

        markerList.push(marker);  // eslint-disable-line
      }
    });

    markers.addLayers(markerList);
    map.addLayer(markers);
  }

  onFetchMedExamData(csvString) {
    this.setState({ data: Papa.parse(csvString, {header: true}).data });
  }

  onFetchAntiViolenceData(csvString) {
    this.setState({ antiviolenceGrants: Papa.parse(csvString, {header: true}).data });
  }

  fetchAntiViolenceData(onSuccess) {
    fetch('data/Antiviolence.csv')
      .then((response) => response.text())
      .then((data) => { onSuccess(data); });
  }

  fetchMedExamData(onSuccess) {
    fetch('data/Medical_Examiner_Case_Archive.csv')
      .then((response) => response.text())
      .then((data) => { onSuccess(data); });
  }

  renderButton(data) {
    const linkStyle = { cursor: 'pointer' };

    return (
      <div style={{margin: 20}} key={data.filterName}>
        <a onClick={this.onClickButton} name={data.filterName} style={linkStyle}>
          {data.text}
        </a>
      </div>
    );
  }

  renderSource(href, text) {
    const divStyle = {color: '#A3A3A3', textTransform: 'uppercase', marginTop: 20};
    const linkStyle = {color: '#A3A3A3'};

    return (
      <div style={divStyle}>
        <a href={href}
           target="_blank"
           style={linkStyle}
           rel="noopener noreferrer"
        >
          {text}
        </a>
      </div>
    );
  }

  renderSources() {
    const {selectedView} = this.state;

    if (!selectedView) return null;

    const divStyle = {color: '#A3A3A3', textTransform: 'uppercase', margin: '40px 20px'};

    if (selectedView === 'gun-related-deaths') return (
      <div style={divStyle}>
        <div>Sources:</div>
        {this.renderSource(
          'https://datacatalog.cookcountyil.gov/Public-Safety/Medical-Examiner-Case-Archive/cjeq-bs86',
          'Cook County Medical Examiner Case Archive'
        )}
      </div>
    );

    if (selectedView === 'antiviolence-programs') return (
      <div style={divStyle}>
        <div>Sources:</div>
        {this.renderSource(
          'https://www.cookcountyil.gov/news/cook-county-board-oks-new-round-community-based-anti-violence-grants',
          'Cook County Press Release, December 2016'
        )}
        {this.renderSource(
          'https://google.com/maps',
          'Google Maps'
        )}
      </div>
    );

    return null;
  }

  render() {
    const buttonData = [
      {
        text: 'GUN-RELATED DEATHS RECORDED BY COOK COUNTY MEDICAL EXAMINER, AUGUST 2014 TO PRESENT',
        filterName: 'gun-related-deaths'
      },
      {
        text: 'PROGRAMS COOK COUNTY IS FUNDING TO STEM VIOLENCE',
        filterName: 'antiviolence-programs'
      }
    ];

    return (
      <div style={{display: 'flex'}}>
        <div style={{
          margin: 30,
          zIndex: 300,
          borderRadius: 5,
          flex: 1
        }}>
          {buttonData.map((data) => { return this.renderButton(data); }, this)}
          {this.renderSources()}
        </div>
        <Map
          style={{flex: 4}}
          ref={map => { this.leafletMap = map; }}
          center={mapCenter}
          zoom={zoomLevel}
          zoomControl={false}
          maxZoom={25}
        >
          <ZoomControl position="bottomleft" />
          <TileLayer
            attribution={tileAttr}
            url={tileURL}
          />
        </Map>
      </div>
    );
  }

}



